.peachpay-lock-body {
	overflow: hidden;
	padding-right: var(--peachpay-scrollbar-width);
}

.hide {
	display: none !important;
}

/**
* This block of styling is for any PeachPay express checkout button
*/
[data-peachpay-button='true'] {
	color: var(--pp-button-text-color) !important;
	background-color: var(--pp-button-background-color) !important;
	transition: all 0.4s ease;
	text-align: center;

	/**
	* This block defines the "None"(Default) effect for the PeachPay buttons.
	*/
	&.effect-none {
		&::before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			border-radius: inherit;
		}

		&:hover:not(.disabled,.loading)::before {
			background: rgb(0 0 0 / 10%);
		}

		span,
		i {
			z-index: 1;
		}
	}

	/**
	* This block defines the "Fade" effect for the PeachPay buttons.
	*/
	&.effect-fade {
		color: var(--pp-button-text-color) !important;
		fill: var(--pp-button-text-color) !important;
		border: 2px solid var(--pp-button-background-color) !important;
		/* stylelint-disable-next-line max-line-length */
		background: linear-gradient(to right, var(--pp-button-text-color) 50%, var(--pp-button-background-color) 50%) !important;
		background-size: 200% 100% !important;
		background-origin: border-box !important;
		background-position: right bottom !important;
		box-shadow: none !important;

		&:hover:not(.disabled,.loading) {
			filter: unset !important;
			color: var(--pp-button-background-color) !important;
			fill: var(--pp-button-background-color) !important;
			border: 2px solid var(--pp-button-background-color) !important;
			background-position: left bottom !important;
		}
	}

	&.loading {
		color: transparent !important;
	}

	/**
	* This block ensures the "View cart" button does not appear after clicking a peachpay shortcode "Add to cart"
	* button. The built in WC Ajax "Add to Cart" functionality will automatically add the "View cart" button to
	* the page. PeachPay builds off of this functionality, so we need to hide the "View cart" button.
	*/
	/* stylelint-disable-next-line selector-class-pattern */
	& + a.added_to_cart {
		display: none !important;
	}
}

#peachpay-checkout-container {
	margin: 0;
	padding: 0;
	width: 0;
	height: 0;

	#peachpay-checkout-backdrop {
		$background: rgb(0 0 0 / 80%);

		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background-color: $background;
		z-index: 2147483647;
		animation: fade-animation 0.4s linear;
		transition: background 0.4s linear;

		&.out {
			background: rgb(0 0 0 / 0%);
		}

		@keyframes fade-animation {
			from {
				background: rgb(0 0 0 / 0%);
			}

			to {
				background: $background;
			}
		}

		.loading-spinner {
			width: 30px;
			height: 30px;
		}

		.loading-messages {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			max-width: 350px;

			a {
				color: white;
				font-weight: bold;
				text-decoration: underline;
				outline: none;
			}

			.message {
				font-size: 15px;
				line-height: 1.5;
				padding: 10px 0;
				margin: 0;
				color: white;
				text-align: center;
			}
		}
	}

	/**
	 * Mobile styling for the Express Checkout iFrame.
	 */

	#peachpay-checkout-iframe {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		z-index: 2147483647;

		@media screen and (max-width: 782px) {
			@supports (width: 100dvw) {
				width: 100dvw;
			}

			@supports (height: 100dvh) {
				height: 100dvh;
			}
		}

		margin: 0;
		padding: 0;
		border: none;
		outline: none;
		animation: slide-animation 0.5s ease-in-out;
		transition: bottom 0.5s ease-in-out, top 0.5s ease-in-out;

		&.out {
			bottom: -100%;
			top: 100%;
		}

		@keyframes slide-animation {
			from {
				bottom: -200%;
				top: 200%;
			}

			to {
				bottom: 0;
				top: 0;
			}
		}
	}
}

// Compatibility for Salient theme on product page
// stylelint-disable-next-line
.woocommerce div.product .nectar-prod-wrap form.cart:not(.variations_form, .grouped_form) {
	flex-direction: column;
}
